import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type BadgeVariant = 'gray' | 'blue' | 'red' | 'yellow' | 'green' | 'purple' | 'pink' | 'orange';
export type BadgeSize = 'sm' | 'md' | 'lg';
export type BadgeEmphasis = 'high' | 'medium' | 'low';

@Component({
  tag: 'bh-badge',
  styleUrl: 'bh-badge.css',
  shadow: true,
})
export class BhBadge {
  /**
   * The color variant of the badge
   */
  @Prop() variant: BadgeVariant = 'gray';

  /**
   * The size of the badge
   */
  @Prop() size: BadgeSize = 'md';

  /**
   * The emphasis level (affects background/border styling)
   */
  @Prop() emphasis: BadgeEmphasis = 'high';

  /**
   * The text label to display
   */
  @Prop() label: string = 'Label';

  /**
   * Material Symbols icon name, or 'dot' for a dot indicator
   */
  @Prop() icon?: string;

  /**
   * Whether the badge can be dismissed
   */
  @Prop() dismissible: boolean = false;

  /**
   * Whether the badge is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Emitted when the dismiss button is clicked
   */
  @Event() bhDismiss!: EventEmitter<void>;

  private handleDismiss = (e: MouseEvent): void => {
    e.stopPropagation();
    if (this.dismissible && !this.disabled) {
      this.bhDismiss.emit();
    }
  };

  render() {
    const badgeClasses = {
      'badge': true,
      [`badge-${this.variant}`]: true,
      [`badge-${this.size}`]: true,
      [`badge-${this.emphasis}`]: true,
      'badge-disabled': this.disabled,
    };

    const iconColorClass = `badge-icon-${this.variant}-${this.emphasis}`;
    const isDot = this.icon === 'dot';

    return (
      <span class={badgeClasses} part="badge">
        {this.icon && (
          <span
            class={{
              'material-symbols-outlined': true,
              [iconColorClass]: true,
              'badge-icon-dot': isDot,
              'badge-icon-custom': !isDot,
            }}
          >
            {isDot ? 'fiber_manual_record' : this.icon}
          </span>
        )}

        <span class="badge-label">{this.label}</span>

        {this.dismissible && (
          <button
            class="badge-dismiss"
            onClick={this.handleDismiss}
            disabled={this.disabled}
            type="button"
            aria-label="Dismiss"
            part="dismiss-button"
          >
            <span class={`material-symbols-outlined badge-dismiss-icon ${iconColorClass}`}>
              close
            </span>
          </button>
        )}
      </span>
    );
  }
}
