/* ==========================================================================
   BH-BADGE COMPONENT STYLES
   ========================================================================== */

:host {
  display: inline-block;
}

/* Base Badge Styles */
.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-sm);
  font-family: var(--font-inter);
  font-weight: var(--weight-medium);
  border-radius: var(--radius-full);
  white-space: nowrap;
  text-align: center;
  transition: all 0.2s ease;
  border: 1px solid transparent;
  user-select: none;
  cursor: default;
}

/* Size Variants */
.badge-sm {
  padding: var(--spacing-xxs) var(--spacing-md);
  font-size: var(--text-sm-size);
  line-height: var(--text-xs-line);
}

.badge-md {
  padding: var(--spacing-xs) var(--spacing-lg);
  font-size: var(--text-sm-size);
  line-height: var(--text-sm-line);
}

.badge-lg {
  padding: var(--spacing-sm) var(--spacing-xl);
  font-size: var(--text-md-size);
  line-height: var(--text-md-line);
}

/* Badge Icon - Dot */
.badge-icon-dot {
  font-size: 8px !important;
  width: 8px;
  height: 8px;
  line-height: 1;
  flex-shrink: 0;
  font-variation-settings: 'FILL' 1, 'wght' 400, 'GRAD' 0, 'opsz' 16;
}

.badge-sm .badge-icon-dot {
  font-size: 6px !important;
  width: 6px;
  height: 6px;
}

.badge-lg .badge-icon-dot {
  font-size: 10px !important;
  width: 10px;
  height: 10px;
}

/* Custom icon styling */
.badge-icon-custom {
  font-size: 16px !important;
  font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 16;
}

.badge-sm .badge-icon-custom {
  font-size: 12px !important;
}

.badge-lg .badge-icon-custom {
  font-size: 20px !important;
}

/* Badge Label */
.badge-label {
  flex-shrink: 0;
  font-weight: inherit;
}

/* Dismiss Button */
.badge-dismiss {
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  margin-left: var(--spacing-xxs);
  border-radius: var(--radius-full);
  transition: all 0.2s ease;
  width: 16px;
  height: 16px;
}

.badge-dismiss-icon {
  font-size: 14px !important;
  width: 14px;
  height: 14px;
  line-height: 1;
  font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 14;
}

.badge-sm .badge-dismiss {
  width: 14px;
  height: 14px;
}

.badge-sm .badge-dismiss-icon {
  font-size: 12px !important;
  width: 12px;
  height: 12px;
}

.badge-lg .badge-dismiss {
  width: 18px;
  height: 18px;
}

.badge-lg .badge-dismiss-icon {
  font-size: 16px !important;
  width: 16px;
  height: 16px;
}

.badge-dismiss:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.badge-dismiss:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

/* ==========================================================================
   HIGH EMPHASIS VARIANTS
   ========================================================================== */

.badge-high.badge-gray {
  background-color: var(--color-neutral-200);
  color: var(--color-neutral-800);
}

.badge-high.badge-blue {
  background-color: var(--color-brand-200);
  color: var(--color-brand-800);
}

.badge-high.badge-red {
  background-color: var(--color-error-200);
  color: var(--color-error-800);
}

.badge-high.badge-yellow {
  background-color: var(--color-warning-200);
  color: var(--color-warning-800);
}

.badge-high.badge-green {
  background-color: var(--color-success-200);
  color: var(--color-success-800);
}

.badge-high.badge-purple {
  background-color: hsl(270, 70%, 85%);
  color: hsl(270, 70%, 35%);
}

.badge-high.badge-pink {
  background-color: hsl(330, 70%, 85%);
  color: hsl(330, 70%, 35%);
}

.badge-high.badge-orange {
  background-color: hsl(25, 90%, 85%);
  color: hsl(25, 90%, 35%);
}

/* Icon colors for high emphasis */
.badge-high .badge-icon-gray-high,
.badge-high .badge-dismiss-icon.badge-icon-gray-high { color: var(--color-neutral-800) !important; }
.badge-high .badge-icon-blue-high,
.badge-high .badge-dismiss-icon.badge-icon-blue-high { color: var(--color-brand-800) !important; }
.badge-high .badge-icon-red-high,
.badge-high .badge-dismiss-icon.badge-icon-red-high { color: var(--color-error-800) !important; }
.badge-high .badge-icon-yellow-high,
.badge-high .badge-dismiss-icon.badge-icon-yellow-high { color: var(--color-warning-800) !important; }
.badge-high .badge-icon-green-high,
.badge-high .badge-dismiss-icon.badge-icon-green-high { color: var(--color-success-800) !important; }
.badge-high .badge-icon-purple-high,
.badge-high .badge-dismiss-icon.badge-icon-purple-high { color: hsl(270, 70%, 35%) !important; }
.badge-high .badge-icon-pink-high,
.badge-high .badge-dismiss-icon.badge-icon-pink-high { color: hsl(330, 70%, 35%) !important; }
.badge-high .badge-icon-orange-high,
.badge-high .badge-dismiss-icon.badge-icon-orange-high { color: hsl(25, 90%, 35%) !important; }

